// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VConsole from 'vconsole'

// require('aframe')
// require('../static/aframe/build/aframe-ar-nft.js')
// import * as ARjs from '../static/aframe/build/aframe-ar-nft.js';

Vue.config.productionTip = false
Vue.config.ignoredElements = [
  'a-scene',
  'a-camera',
  'a-box',
  'a-image',
  'a-sphere',
  'a-cylinder',
  'a-plane',
  'a-sky',
  'a-nft',
  'a-entity',
]

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})
// new Vue({
//   render: function (h) { return h(App) },
// }).$mount('#app')

new VConsole()



